<template>
  <label>
    {{ label }}
    <input
      v-model="input"
      list="id"
      :class="classes"
      :name="name"
      v-bind="$attrs"
      @input="onInputChange($event)"
    />
    <datalist id="id">
      <option
        v-for="option in options"
        :key="option.key"
        :data-value="option.value"
      >
        {{ option.text }}
      </option>
    </datalist>
    <ui-error v-if="errors" :errors="errors" />
  </label>
</template>

<script>
import { computed } from "vue";
import UiError from "./UiError";

export default {
  name: "UiDatalist",
  components: {
    UiError,
  },
  inheritAttr: false,
  props: {
    // eslint-disable-next-line
    code: {
      type: String,
    },
    errors: {
      type: [Array, String],
      default: "",
      required: false,
    },
    id: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      default: "",
      required: false,
    },
    name: {
      type: String,
      required: true,
    },
    options: {
      type: [Array],
      required: true,
    },
    // eslint-disable-next-line
    value: {
      type: String,
    },
  },
  emits: ["update:value", "update:code"],
  setup(props, { emit }) {
    const input = computed({
      get: () => props.value,
      set: (value) => emit("update:value", value),
    });
    const onInputChange = (evt) => {
      const match = Array.from(evt.target.list.options).find(
        (o) => o.innerHTML === evt.target.value
      );
      emit("update:code", match ? match.dataset["value"] : "");
    };

    return {
      input,
      onInputChange,
    };
  },
  computed: {
    classes() {
      return {
        "ui-datalist": true,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
label {
  font-weight: bold;
}

.ui-datalist {
  outline: 1px solid gray;
}
</style>
